.backdrop {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(46, 47, 66, 0.4);
  position: relative;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transition: opacity 0.25s cubic-bezier(0.4, 0, 0.2, 1),
    visibility 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 10001;
}

.is-visible {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}

.form-text-span {
  color: rgba(5, 5, 5, 0.5);
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
}

.form-control {
  padding: 8px 0 16px 0;
}

.form-control-comment {
  padding-top: 8px;
  padding-bottom: 40px;
}

.form-input {
  display: flex;
  width: 100%;
  height: 46px;
  padding: 14px 18px 14px 18px;
  align-items: flex-start;
  gap: 32px;
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.2);
  outline: transparent;
  transition: border-color $cubic-bezier;
  &:hover {
    border-color: $accentColor;
  }
  &:focus {
    border-color: $accentColor;
  }
}

.container-order-modal {
  max-width: 335px;
  height: 582px;
  border-radius: 15px;
  background: #fff;
  padding: 28px 20px;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
  overflow: hidden;
}

.form-feedback {
  display: flex;
  width: 100%;
  height: 100px;
  padding: 14px 18px 14px 18px;
  align-items: flex-start;
  gap: 32px;
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.2);
  resize: none;
  outline: transparent;
  transition: border-color $cubic-bezier;
  &:hover {
    border-color: $accentColor;
  }
  &:focus {
    border-color: $accentColor;
  }
}

.modal-close-icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  right: 0;
  transform: rotate(180ded);
  stroke: #050505;
  transition: all $cubic-bezier;
  &:hover {
    stroke: #9bb537;
    transform: rotate(180deg);
  }
  &:hover {
    stroke: #9bb537;
    transform: rotate(180deg);
  }
}

.form-btn {
  display: flex;
  width: 100%;
  margin: auto;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 15px;
  background-color: #9bb537;
  border: none;
  color: #fff;
  transition: color $cubic-bezier;
  padding-bottom: 15px;
  padding-top: 15px;
  &:hover {
    color: #050505;
  }
  &:hover {
    color: #050505;
  }
}

.modal-order-title {
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  padding-bottom: 28px;
  color: #050505;
}

.modal-close {
  position: absolute;
  right: 14px;
  top: 14px;
  background-color: #fff;
  border-radius: 50%;
  align-items: center;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  border: none;
}
//

.container-rating-modal {
  max-width: 335px;
  height: 216px;
  border-radius: 15px;
  background: #fff;
  padding: 20px;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-rating-title {
  padding-bottom: 4px;
}

.form-rating-control {
  padding: 21px 0 14px 0;
}

@media screen and (min-width: 768px) {
  .container-order-modal {
    padding: 40px;
    max-width: 440px;
    height: 632px;
  }

  .modal-order-title {
    padding-bottom: 40px;
  }

  .form-control {
    padding-bottom: 18px;
  }

  .form-input {
    height: 48px;
    padding: 14px 18px 14px 18px;
  }

  .container-rating-modal {
    padding: 32px;
    max-width: 424px;
    height: 254px;
  }

  .modal-close {
    right: 16px;
    top: 16px;
  }

  .form-rating-control {
    padding-top: 16px;
    padding-bottom: 32px;
  }
  .modal-close-icon {
    width: 24px;
    height: 24px;
  }
  .form-feedback {
    padding: 14px 18px 14px 18px;
  }
}
@media screen and (min-width: 1280px) {
}

.rating__value {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  padding-right: 10px;
  line-height: 1;
}
.rating-modal {
  display: flex;
  align-items: flex-end;
}
.rating__body {
  position: relative;
  &::before {
    content: '★★★★★';
    display: block;
    color: #d9d9d9;
  }
}

.rating__active {
  position: absolute;
  width: 0%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
  &::before {
    content: '★★★★★';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    color: #eea10c;
  }
}
.rating__items {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.rating__item {
  flex: 0 0 20%;
  height: 100%;
  opacity: 0;
}
